sizing: border-box;\n  scroll-behavior: smooth;\n  overflow-x: hidden;\n}\n\n*, *:before, *:after {\n  box-sizing: inherit;\n  font-family:$body;\n}\n\nbody, h1, h2, h3, h4, ul, p{\n\tmargin:0;\n\tpadding:0;\n}\n\n// Dynamic colors\n.color-1{\n  background-color:$green;\n}\n.color-2{\n  background-color:$navy;\n}\n.color-3{\n  background-color:$blue;\n}\n.color-4{\n  background-color:$lavender;\n}\n.color-5{\n  background-color:$aquamarine;\n}\n.color-6{\n  background-color:$magenta;\n}\n.color-7{\n  background-color:$grey;\n}\n.color-8{\n  background-color:$orange;\n}\n.color-9{\n  background-color:$yellow;\n}\n.color-10{\n  background-color:$pink;\n}\n\n.slide{\n\tmin-height:100vh;\n\tdisplay:flex;\n\tjustify-content: center;\n\talign-items: center;\n  flex-wrap: wrap;\n  position: relative;\n  background-repeat: no-repeat;\n  overflow:hidden;\n  padding:50px 0;\n}\n\n.card{\n\tbackground:$grey;\n\tmin-height:80vh;\n\tmin-width:50vw;\n  padding:50px;\n\twidth:80%;\n  position:relative;\n  z-index: 2;\n}\n\n// Typography\nh1{\n  font-size: 60px;\n  margin: 0px auto;\n  font-weight:200;\n  width:100%;\n  font-family:$header;\n  color:$navy;\n}\n.ital{\n  font-style: italic;\n  font-family: $header; \n  margin-top:8px;\n  line-height:1.4;\n}\nh2{\n  font-family:$body;\n  font-size: 32px;\n  font-weight:200;\n  width:100%;\n  text-align: center;\n}\n\nh3 {\n  margin: 0px auto 10px;\n}\n\nh4 {\n  font-variant-numeric: tabular-nums;\n}\n\n\n// Header\nheader {\n  background: $pink;\n  font-family: $header; \n  text-align: center;\n  color:$navy;\n  h1{\n  \tfont-size: 70px;\n  \tmargin: 0px auto;\n  \tfont-weight:200;\n  \twidth:100%;\n  \tfont-family:$header;\n  }\n  .ital{\n    font-style: italic;\n    font-family: $header; \n  }\n  h2{\n  \tfont-family:$body;\n  \tfont-size: 32px;\n  \tfont-weight:200;\n  \twidth:100%;\n  }\n  h3, h4 {\n    width: 100%;\n    font-weight: 200;\n    font-size: 23px;\n    line-height: 1.6;\n  }\n  .card{\n  \tbox-shadow: 5px 4px 8px 1px darken($pink,20%);\n  \tdisplay: flex;\n  \tflex-wrap: wrap;\n  \tpadding: 80px 50px;\n    overflow:hidden;\n  }\n\n  .nav{\n  \t// width: 100%;\n  \tdisplay: flex;\n  \tjustify-content: space-between;\n  \tmargin:0 auto 25px;\n  \t.nav-item{\n  \t\ttext-decoration: none;\n  \t\tcolor:$navy;\n  \t\tfont-size:20px;\n      transition:all 0.25s;\n      margin:0 40px;\n  \t}\n    .nav-item:hover{\n      transform:scale(0.9);\n      transition:all 0.25s;\n      color:darken($navy, 15%);\n    }\n  }\n\n  .nav-item{\n    position:relative;\n    .link-text{\n      position:relative;\n      z-index: 5;\n      padding:20px 5px;\n    }\n    &:hover{\n      .link-text{\n        color:white;\n      }\n      .link-fill{\n        width:600px;\n        height:600px;\n        transition: width 0.7s cubic-bezier(0.175, 0.885, 0.32, 1.275);\n        transition-property: width, height;\n      }\n    }\n    .link-fill{\n      background:$blue;\n      position:absolute;\n      pointer-events: none;\n      display: block;\n      width:0px;\n      height:0px;\n      left: 50%;\n      transform: translate(-50%, -50%);\n      border-radius: 50%;\n      transition: width 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);\n      transition-property: width, height;\n      opacity:0.8;\n    }\n\n    &.link-location{\n      .link-fill{\n        background: $magenta;\n      }\n    }\n\n    &.link-boston{\n      .link-fill{\n        background: $yellow;\n      }\n    }\n\n    &.link-contact{\n      .link-fill{\n        background: $aquamarine;\n      }\n    }\n  }\n\n}\n\n.btn{\n  width:100%;\n  text-decoration: none;\n  span{\n    width:120px;\n    background:$pink;\n    color:white;\n    cursor:pointer;\n    padding:10px;\n    font-size:25px;\n\n  }\n}\n\n\nsection .btn{\n  position: absolute;\n  top:30px;\n  right:20px;\n  width:auto;\n}\n\n.bars{\n\n\twidth: 100%;\n\tmargin:20px auto;\n\thr{\n\t\twidth:40%;\n\t\tborder-width: 0.5px;\n\t\tborder-color:$pink;\n\t}\n}\n\n\n// Background\n.bg-group{\n  display: flex;\n  width:100vw;\n  flex-wrap: wrap;\n  position:absolute;\n  z-index:1;\n  top:0px;\n  overflow: hidden;\n\n  &:hover .sparkle{\n    opacity: 1;\n  }\n\n  // &:hover .spin{\n  //   animation: rotating 1s infinite;\n  //   animation-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);\n  // }\n\n  &:hover .confetti{\n\n    &.color-1, &.color-6 {\n        animation: rotating .5s infinite;\n      }\n\n      &.color-2, &.color-7 {\n        animation: rotating .8s infinite;\n      }\n\n      &.color-3, &.color-8 {\n        animation: rotating 1s infinite;\n      }\n\n      &.color-4, &.color-9 {\n        animation: rotating .2s infinite;\n      }\n\n      &.color-5, &.color-10 {\n        animation: rotating .1s infinite;\n      }\n\n      &.color-5 {\n        animation: rotating 0.8s infinite;\n      }\n  }\n}\n.confetti{\n  margin:0px 31px;\n  width:5px;\n  height:20px;\n  border-radius:500px;\n  transform:rotate(45deg);\n  &:hover{\n   \n  }\n\n  &:nth-child(even){\n    height:40px;\n  }\n\n  &.color-1, &.color-6 {\n    transform:rotate(30deg);\n  }\n\n  &.color-2, &.color-7 {\n    transform:rotate(10deg);\n  }\n\n  &.color-3, &.color-8 {\n    transform:rotate(120deg);\n  }\n\n  &.color-4, &.color-9 {\n    transform:rotate(90deg);\n  }\n\n  &.color-5, &.color-10 {\n    transform:rotate(60deg);\n  }\n\n  &.color-5 {\n    transform:rotate(200deg);\n  }\n  \n}\n\n\n\n.diamond{\n  width: 30px;\n  height: 30px;\n  border: 1px solid $magenta;\n  transform: rotate(45deg);\n  opacity:1;\n  opacity:0.5;\n\n  \n\n  &:nth-child(even){\n    transform: rotate(60deg);\n  }\n\n  &:nth-child(5n+1){\n    border-color:white;\n    opacity:0.3;\n  }\n\n  /*&:nth-child(5n+1){\n    border-color:$yellow;\n  }\n\n  &:nth-child(5n+2){\n    border-color:$pink;\n  }\n\n  &:nth-child(5n+3){\n    border-color:$aquamarine;\n  }\n\n  &:nth-child(5n+4){\n    border-color:$magenta;\n  }\n\n  &:nth-child(5n+5){\n    border-color:$orange;\n  }*/\n}\n\n.btn {\n  margin: 20px;\n}\n\n//cards\n.column-grid{\n  text-align:center;\n}\n\n.column {\n  display: flex;\n  flex-flow: row wrap;\n  align-items: flex-start;\n  align-content: flex-start;\n  width: 100%;\n  margin-bottom: 20px;\n\n}\n\n.event {\n  display: flex;\n  flex-flow: row wrap;\n  width: 100%;\n  justify-content: center;\n  margin:5px;\n  border:1px solid $blue;\n  min-height:450px;\n  align-items: center;\n}\n\n.event-large{\n  border: none;\n  min-height: auto;\n}\n\n.text {\n  width: 30%;\n  min-width: 300px;\n  margin-right: 20px;\n  text-align:left;\n  margin-top:-15px;\n}\n\n.map {\n  width: 100%;\n  max-width: 199px;\n  margin: 20px;\n  iframe{\n    border: 3px solid #ff4141 !important;\n    transform: rotate(-10deg);\n    filter: hue-rotate(236deg) saturate(5);\n\n  }\n\n  &.reverse iframe{\n    transform:rotate(10deg);\n  }\n}\n// Schedule\n\n#schedule, #schedule .btn span{\n\tbackground-color:$blue;\n\n  .bold{\n    color:$blue;\n  }\n  .card{\n    box-shadow: 5px 4px 8px 1px darken($blue,10%);\n  }\n\n  h2{\n    padding: 0 0 6px 0;\n    color: $blue;\n    max-width:780px;\n    margin:50px auto 0;\n  }\n\n  .column{\n    max-width: 400px;\n    display: inline-block;\n    vertical-align: top;\n  }\n}\n\n// rsvp\n#location, #location .btn span{\n\tbackground-color:$magenta;\n  .card{\n    box-shadow: 5px 4px 8px 1px darken($magenta,20%);\n  }\n\n  .map .iframe{\n    filter: hue-rotate(274deg) saturate(5);\n  }\n\n  h2{\n    padding: 0 0 6px 0;\n    color: $magenta;\n    max-width:780px;\n    margin:50px auto 0;\n  }\n}\n\n// details\n#boston, #boston .btn span{\n\tbackground-color:$yellow;\n  .card{\n    box-shadow: 5px 4px 8px 1px darken($yellow,20%);\n  }\n}\n\n// location\n#contact, #contact .btn span{\n\tbackground-color:$aquamarine;\n  .card{\n    box-shadow: 5px 4px 8px 1px darken($aquamarine,20%);\n  }\n}\n\n// Rsvp\n#rsvp, #rsvp .btn span{\n\tbackground-color:$pink;\n  .card{\n    box-shadow: 5px 4px 8px 1px darken($pink,20%);\n  }\n}\n\n.heart {\n  position: relative;\